header {
    height: 100px;
    display: flex;
    align-items: center;
    background: #6441A5;
    background: -webkit-linear-gradient(to right, #2a8845, #6441a5);
    background: linear-gradient(to right, magenta, purple)
}

header > img {
    height: 80%;
    margin-left: 10%;
}

header > p {
    font-family: monospace;
    color: whitesmoke;
    font-size: 50px;
    margin-left: 60px;
}

.meme {
    position: relative;
    width: 90%;
    margin: auto;
}

.meme > img {
    width: 100%;
}

.meme > h2 {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}

.meme > .bottom {
    bottom: 0;
}

.meme > .top {
    top: 0;
}

.meme-form {
    width: 90%;
    margin: 26px auto;
    display: flex;
    justify-content: space-between;
}

.meme-form > input {
    width: 45%;
    height: 40px;
}

.meme-form > button {
    border: none;
    font-family: monospace;
    font-size: 25px;
    letter-spacing: 1.5px;
    color: white;
    background: #6441A5;
}

.meme-form > input::-webkit-input-placeholder {
    font-family: monospace;
    font-size: 25px;
    text-align: cen;
}

.meme-form > input::-moz-placeholder {
    font-family: monospace;
    font-size: 25px;
    text-align: cen;
}

.meme-form > input::-ms-input-placeholder {
    font-family: monospace;
    font-size: 25px;
    text-align: cen;
}

.meme-form > input::-moz-placeholder {
    font-family: monospace;
    font-size: 25px;
    text-align: cen;
}